.contacts {
  &__content {
    @include page-grid;

    row-gap: 16px;
  }

  &__input {
    font-family: "Space Mono", sans-serif;
    grid-column: 1 / -1;
    resize: none;

    @include on-desktop {
      grid-column: 1 / 6;
    }

    background-color: #F8F8FA;
    border-radius: 8px;
    border: 1px solid #F8F8FA;
    transition-duration: 500ms;

    &::placeholder {
      color: #7E7E83;
    }

    &:focus {
      outline: 1px solid $main-color;

      &::placeholder {
        color: $main-color;
      }
    }

    &:hover {
      border: 1px solid #D5D7DE;
      &::placeholder {
        color: $main-color;
      }
    }

    &--small {
      height: 48px;
    }

    &--large {
      height: 148px;
      margin-bottom: 16px;
    }
  }

  &__button {
    @include button-style;

    font-style: Bold;
    font-size: $main-font-size;
    font-weight: 700;
    line-height: 27px;
    grid-column: 1 / -1;
    margin-bottom: 32px;

    @include on-tablet {
      margin-bottom: 74px;
    }

    @include on-desktop {
      margin-bottom: 120px;
      grid-column: 1 / 6;
    }
  }

  &__description {
    display: flex;
    flex-direction: column;
    grid-column: 1 / 2;
    grid-row: 5 / 6;

    @include on-desktop {
      grid-column: 7 / 12;
      grid-row: 1 / 5;
    }
  }

  &__paragraph {
    margin: 0;
    padding: 0;
    font-family: "Space Mono", sans-serif;
    font-weight: $main-font-weight;
    font-size: $main-font-size;
    line-height: $main-line-height;
    color: #191919;
    margin-bottom: 64px;

    @include on-tablet {
      margin-bottom: 120px;
    }

    @include on-desktop {
      margin-bottom: 0;
    }
  }

  &__link {
    margin: 0;
    font-family: "Space Mono", sans-serif;
    font-weight: $main-font-weight;
    font-size: $main-font-size;
    line-height: $main-line-height;
    text-decoration: none;
    color: #191919;
    margin-bottom: 24px;
  }
}
